import React, { Component } from "react";
import {
  Card,
  CardImg,
  CardText,
  CardBody,
  CardTitle,
  CardSubtitle,
  Button
} from "reactstrap";
import { FaPenSquare, FaRegEdit } from "react-icons/fa";

class ListItem extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    const { id, data_field, id_users } = this.props.list_massage;
    return (
      <div>
        <Card className="blockpost">
          <div>
            <a style={{ textAlign: "right" }} name={id}>
              <FaPenSquare />
            </a>
          </div>
          <div>
            <a style={{ textAlign: "right" }} name={id}>
              <FaRegEdit/>
            </a>
          </div>
          <CardBody>
            <CardTitle>{id_users}</CardTitle>
            <CardText>
              <span>{data_field}</span>
              <span>{}</span>
            </CardText>
          </CardBody>
        </Card>
      </div>
    );
  }
}

export default ListItem;
